.c-logo--title {
  position: absolute;
  top: 30%;
  left: 10%;
  color: #e2f020;
  font-family: "Architects Daughter", cursive;
  font-size: 25px;
  font-weight: bold;
}

.neon {
  display: inline-block;
  color: #fff;
  letter-spacing: 0.05em;
  font-size: 5em;
  text-shadow: 0 1px 30px #FB1684, 0 0 12px #fff, 2px 5px 60px #990a52;
}

.neon > span {
  -webkit-animation: blink 3s infinite alternate;
  animation: blink 3s infinite alternate;
}

@-webkit-keyframes blink {
  40% {
    opacity: 0.85;
  }
  42% {
    opacity: 0.4;
  }
  43% {
    opacity: 0.85;
  }
  45% {
    opacity: 0.4;
  }
  46% {
    opacity: 0.85;
  }
}
@keyframes blink {
  40% {
    opacity: 0.85;
  }
  42% {
    opacity: 0.4;
  }
  43% {
    opacity: 0.85;
  }
  45% {
    opacity: 0.4;
  }
  46% {
    opacity: 0.85;
  }
}